
.footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  margin-top: 200px;
  padding-bottom: 80px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 16px;
  color: #787b7f;
}
.footer-logo {
    width: 109px;
    height: 48px;
    margin-bottom: 20px;
    background: url(../../assets/icons/tigeeer_green.png) no-repeat;
    background-size: cover;
}
.footer-item {
    max-width: 281px;
    margin-right: 80px;
}
.footer-item:last-child {
      margin-right: 0;
}
.footer-tips {
    color: rgba(255, 255, 255, 0.87);
    font-weight: 500;
    margin-bottom: 30px;
}
.footer-options-item {
    margin-bottom: 18px;
    color: #787b7f;
}
.footer a:hover li {
    color: rgba(255, 255, 255, 0.87);
}
.footer .lh22 {
    line-height: 22px;
}
.footer-share {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 50px;
}
.footer-share > li {
      width: 30px;
      height: 30px;
      margin-right: 20px;
      border-radius: 50%;
      cursor: pointer;
}
.footer-share-wechat {
      background: #dedede;
      background: url(../../assets/icons/wechat.png) no-repeat;
      background-size: contain;
}
.footer-share-wechat:hover {
        background: url(../../assets/icons/wechat_hover.png) no-repeat;
        background-size: contain;
}
.footer-share-weibo {
      background: #dedede;
      background: url(../../assets/icons/weibo.png) no-repeat;
      background-size: contain;
}
.footer-share-weibo:hover {
        background: url(../../assets/icons/weibo_hover.png) no-repeat;
        background-size: contain;
}
.footer-share-qq {
      background: #dedede;
      background: url(../../assets/icons/qq.png) no-repeat;
      background-size: contain;
}
.footer-share-qq:hover {
        background: url(../../assets/icons/qq_hover.png) no-repeat;
        background-size: contain;
}
.footer-share-qzone {
      background: #dedede;
      background: url(../../assets/icons/qzone.png) no-repeat;
      background-size: contain;
}
.footer-share-qzone:hover {
        background: url(../../assets/icons/qzone_hover.png) no-repeat;
        background-size: contain;
}



