
.tigger {
  max-width: 1450px;
  margin: 0 auto;
  padding: 0 100px 0;
  border-top: 1px solid #1d2126;
}
.tigger-item {
    margin-top: 200px;
}
.tigger-item:first-child {
      margin-top: 170px;
}
.tigger-flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.tigger-title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin-right: 90px;
    font-size: 28px;
    font-weight: bold;
    color: #2cc086;
}
.tigger-descrip {
    -webkit-box-flex: 2;
        -ms-flex: 2;
            flex: 2;
    font-size: 16px;
    line-height: 1.625;
    color: rgba(255, 255, 255, 0.87);
}
.tigger-img {
    width: 100%;
    margin-top: 42px;
}



