
.user {
  background: #13161b;
  border-top: 1px solid #1d2126;
}
.user-wrapper {
    padding: 0 50px;
}
.user-info {
    margin-top: 30px;
}
.user-avatar {
    width: 100px;
    height: 100px;
    margin: 0 auto;
}
.user-name {
    margin-top: 18px;
    text-align: center;
    font-size: 35px;
    font-weight: bold;
    line-height: 1;
    color: #e4e4e4;
}
.user-position {
    margin-top: 8px;
    font-size: 18px;
    line-height: 1;
    letter-spacing: 0.1px;
    text-align: center;
    color: #787b7f;
}
.user-nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 69px;
    padding: 7px 0;
    background-color: #191c20;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.user-nav div {
      display: block;
      width: 130px;
      height: 38px;
      line-height: 38px;
      vertical-align: middle;
      font-weight: bold;
      text-align: center;
      border-radius: 4px;
      cursor: pointer;
}
.user-nav div.active {
        background: #2cc086;
        color: rgba(255, 255, 255, 0.87);
}
.user-edit {
    height: 40px;
    padding-right: 50px;
    margin-bottom: 30px;
    text-align: right;
}
.user-edit button {
      width: 140px;
      height: 40px;
      font-weight: bold;
      background: #2cc086;
      border-radius: 4px;
      outline: none;
      text-align: center;
}
.user .icon-edit {
    display: inline-block;
    width: 19px;
    height: 19px;
    margin-right: 6px;
    vertical-align: middle;
    background: url(../../assets/icons/edit.png) no-repeat;
    background-size: contain;
}



