
.pay-line {
  border: 1px solid #1d2126;
}
.pay {
  padding-top: 140px;
  width: 500px;
  margin: 0 auto;
}
.pay-title {
    height: 50px;
    margin: 146px 0 100px;
    text-align: center;
    font-size: 50px;
    font-weight: 500;
    line-height: 1;
    color: #fff;
}
.pay-tips {
    margin-bottom: 19px;
    font-size: 30px;
    color: #2cc086;
}
.pay-good {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 500px;
    padding: 15px 20px;
    margin: 10px 0 60px;
    text-align: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background: #191c20;
    border-radius: 4px;
}
.pay-good-3 {
      -webkit-box-flex: 3;
          -ms-flex: 3;
              flex: 3;
      text-align: left;
}
.pay-good-1 {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.pay-left {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.pay-left-img {
      width: 150px;
      height: 94px;
      margin-right: 25px;
}
.pay-left-info {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.pay-left-name {
      font-size: 18px;
      line-height: 1;
}
.pay-left-name {
      text-align: left;
      margin-bottom: 13px;
      font-size: 14px;
      line-height: 1;
      color: #787b7f;
}
.pay-left-name:last-child {
        margin-bottom: 0;
}
.pay-right {
    margin-left: 16px;
}
.pay-right-name {
      text-align: left;
      margin-bottom: 13px;
      font-size: 14px;
      line-height: 1;
}
.pay-right-name:last-child {
        margin-bottom: 0;
}
.pay-price {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 56px;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0 20px;
    font-size: 18px;
    border-radius: 4px;
    background-color: #191c20;
}
.pay-ways {
    padding: 22px 22px 0;
    margin-top: 20px;
    border-radius: 4px;
    background: #191c20;
}
.pay-ways-title {
      font-size: 18px;
      margin-bottom: 10px;
}
.pay-ways-item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      height: 97px;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      color: #dedede;
      font-size: 22px;
      background-color: #191c20;
      border-bottom: 1px solid #272c33;
}
.pay-ways-item:last-child {
        border-bottom: none;
}
.pay-ways-radio {
      position: relative;
}
.pay-ways-radio button {
        width: 20px;
        height: 20px;
        border-radius: 4px;
        border: solid 2px #5e6063;
}
.pay-ways-radio input {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        opacity: 0;
        z-index: 3;
        cursor: pointer;
}
.pay-ways-radio input:checked + button {
        background-color: #2cc086;
        border: solid 2px rgba(255, 255, 255, 0.87);
}
.pay-btn {
    margin-top: 40px;
    padding: 22px;
    background: #191c20;
    border-radius: 4px;
}
.pay-btn button {
      width: 100%;
      height: 50px;
      line-height: 50px;
      color: #fff;
      border-radius: 4px;
      background-color: #2cc086;
}
.pay .icon-alipay {
    display: inline-block;
    width: 116px;
    height: 35px;
    margin-right: 10px;
    vertical-align: middle;
    background: url(../../assets/icons/zhifubao.png);
    background-size: cover;
}
.pay .icon-paypal {
    display: inline-block;
    width: 160px;
    height: 35px;
    margin-right: 10px;
    vertical-align: middle;
    background: url(../../assets/icons/paypal.png);
    background-size: cover;
}



