
.about {
  max-width: 1425px;
  margin: 0 auto;
  padding: 0 87px 0;
  border-top: 1px solid #1d2126;
}
.about-triangle {
    width: 41px;
    height: 43px;
    margin: 35px auto 0;
    background: #191c20;
    background: url(../../assets/images/about/triangle.png) no-repeat;
    background-size: contain;
}
.about-title {
    width: 950px;
    margin: 130px auto 158px;
    color: rgba(255, 255, 255, 0.87);
    font-size: 34px;
    text-align: center;
    font-weight: 500;
    line-height: 1.32;
}
.about-portfolio {
    padding: 70px;
    background: #191c20;
}
.about-portfolio img {
      width: 100%;
      box-shadow: 0 0 55px rgba(0, 0, 0, 0.15);
}
.about-sell {
    padding: 70px;
    margin-top: 100px;
    background: #191c20;
}
.about-sell img {
      width: 100%;
}
.about-flex {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 40px;
}
.about-flex-left {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      font-size: 30px;
      font-weight: bold;
      line-height: 1;
}
.about-flex-right {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      font-size: 16px;
      line-height: 1.5;
}
.about-swiper {
    width: 100%;
    overflow: hidden;
}
.about-swiper-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    transition: all .5s;
}
.about-swiper-slider {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
}
.about-team {
    width: 820px;
    margin: 175px auto 0;
}
.about-team-title {
      height: 40px;
      font-size: 40px;
      font-weight: 600;
      line-height: 1;
      text-align: center;
}
.about-team-member {
      position: relative;
      margin-top: 90px;
      text-align: right;
}
.about-team-member:nth-child(even) {
        text-align: left;
}
.about-team-member:nth-child(even) .about-team-avatar {
          right: 0;
          padding-left: 150px;
          padding-right: 0;
          background-size: cover;
}
.about-team-member:nth-child(even) .about-team-info {
          padding: 40px 157px 40px 57px;
}
.about-team-member-name {
        margin-bottom: 6px;
        font-size: 28px;
        line-height: 1;
        font-weight: bold;
        letter-spacing: 2.5px;
}
.about-team-member-position {
        font-size: 15px;
        line-height: 1;
        color: #9b9b9b;
}
.about-team-member-descrip {
        max-height: 84px;
        margin-top: 34px;
        font-size: 14px;
        color: #9b9b9b;
        line-height: 21px;
        overflow: hidden;
}
.about-team-avatar {
      position: absolute;
      top: 25px;
      width: 404px;
      height: 228px;
      padding-right: 150px;
      background-color: red;
      background-size: cover;
}
.about-team-info {
      display: inline-block;
      width: 532px;
      height: 280px;
      padding: 25px 57px 25px 157px;
      font-size: 14px;
      line-height: 1.36;
      background-color: #191c20;
      text-align: left;
}
.about-team-flex {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      height: 100%;
}
.about-team-box {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.about-team-pagination {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      margin-top: 40px;
      text-align: center;
}
.about-team-pagination-item {
        width: 10px;
        height: 10px;
        margin: 0 10px;
        border-radius: 12px;
        background: #4a4a4a;
        cursor: pointer;
}
.about-team-pagination-item.active {
          background: #2cc086;
}



