
.photography-banner {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-transform: translateY(-64px);
          transform: translateY(-64px);
}
.photography-banner img {
    width: 100%;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.photography-banner-cover {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(to bottom, rgba(19, 22, 27, 0), #13161b);
}
.photography-wrap {
  position: absolute;
  top: 100%;
  width: 100%;
  padding: 56px 50px 0;
  background: #13161b;
}
.photography-scroll-notice {
  position: absolute;
  left: 50%;
  bottom: 20px;
  width: 160px;
  height: 40px;
  margin-left: -80px;
  font-size: 12px;
  line-height: 40px;
  text-align: center;
  border-radius: 100px;
  background-color: rgba(16, 19, 24, 0.7);
  box-shadow: 4px 4px 10px 0 rgba(0, 0, 0, 0.4);
}
.photography-scroll-notice .icon-mouse {
    display: inline-block;
    width: 15px;
    height: 23px;
    margin-right: 8px;
    vertical-align: middle;
    background: url(../../assets/icons/mouse.png) no-repeat;
    background-size: cover;
}
.waterfall-item {
  background: #191c20;
  cursor: pointer;
}
.waterfall-item:hover .notice {
    opacity: 1;
}
.waterfall-item .notice {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    display: table;
    font-size: 16px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.6);
    transition: all .5s;
}
.waterfall-item .notice span {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
}



